.Header
{
    background-color: transparent;
    position: relative;
    padding: 0;
    top:0;
    left:0;
    width: 100vw;
    height: 80px;
    .nav-toggle
    {
        background-color: transparent;
        color: transparent;
        border: none;
        outline: none;
        line-height: 0;
        padding: 0;
        &-icon
        {
            display: inline-block;
            width: 25px;
            border-radius: 20px;
            height: 3px;
            background-color: $primary-color;
            position: relative;
            &::after
            {
                content: '';
                display: inline-block;
                position: absolute;
                left: 0;
                top: 6px;
                width: 25px;
                border-radius: 20px;
                height: 3px;
                background-color: $primary-color;
                transform: rotate(0deg) translateX(0px);
                transition: transform 500ms ease-in-out;
            }
            &::before
            {
                @extend .nav-toggle-icon::after;
                top: -6px;
            }
            &.active
            {
                background-color: transparent;
                &::after
                {
                    transform: rotate(50deg) translateX(-7px);
                }
                &::before
                {
                    transform: rotate(-50deg) translateX(-8px);
                }
            }
        }
  
    }
    .nav-items
    {
        display: flex;
        align-items: center;
        @include media('tab-port')
        {
            align-items: flex-start;
            background-color:$secondary-color-light;
        }
        &-item
        {
            list-style: none;
            margin-right: 1rem;
            position: relative;
            z-index: 1;
            &:last-of-type
            {
                margin-right: 0;
            }
            .nav-item-link
            {
                font-size: .75rem;
                font-weight: 700 ;
                color: $secondary-color-dark;
                text-transform: uppercase;
                transition: all 500ms ease-in-out;
           
            }
            .nav-item-link.active
            {
                color: $primary-color;
            }
        }       
        &-item::after
        {
            content:'';
            position: absolute;
            width: 0;
            height: 2px;
            background-color: $primary-color;
            z-index: 1;
            top: 50%;
            right: 0;
            transform: translateX(-8px);
            opacity: 0;
            transition:opacity,width 500ms ease-in-out
        }
        &-item:hover::after, &-item.active::after
        {
            opacity: 1;
            width: 100%;
            @include media('tab-port')
            {
                opacity: 0;
            }
        }
        &-item:hover .nav-item-link
        {
            color: $primary-color;
        }
        .item-space
        {
            margin-right: 1rem;
        }
        @include media('tab-port')
        {
            .item-space
            {
                display: none;
            }
        }
        // li.Blog to appear dropDown
        .Blog
        {
            &-dropDown
            {
                width: 210px;
                display: flex;
                flex-direction: column;
                position: absolute;
                top:100%;
                left: -40px;
                box-shadow: 1px 1px 10px rgba($secondary-color-light,0.5);
                pointer-events: none;
                opacity: 0;
                transform: translateY(40px);
                transition: transform 500ms ease-in-out;
                &-item
                {
                    list-style: none;
                    padding:1rem;
                    width: 100%;
                    border-bottom: 1px solid rgba($primary-light-color,.6);
                    background-color: white;
                    transition: background-color 200ms ease-in-out;
                    cursor: pointer;
                    a
                    {
                        color: $secondary-color-dark;
                        font-weight: 700;
                        text-transform: uppercase;
                        font-size: .75rem;
                        transition: color 200ms ease-in-out;
                    }
                    &:hover
                    {
                        background-color: $primary-color;
                    }
                    &:hover a
                    {
                       color:$secondary-color-light;
                    }
                }
            }
            &:hover .Blog-dropDown
            {
                opacity: 1;
                transform: translateY(21px);
                pointer-events: visible;
            }
        }
    }
    .Seacrh
    {
        display: flex;
        justify-content: center;
        #searchBtn
        {
            border: none;
            background-color: transparent;
            outline: none;
            margin-left:5rem;
            text-align: center;
            @include media('tab-port')
            {
                margin-left:0;
            }
            i
            {
                color: $secondary-color-dark;
                font-weight: 600;
            }
        }
    }
}
.Search-div
{
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
    transition: opacity 500ms ease-in-out;
    &.active
    {
       opacity: 1;
       pointer-events: visible;
    }
    #searchInput
    {
        padding: .2rem 1rem;
        background-color: $primary-color;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 100%;
        margin:0 0;
        transform:translateY(-90px);
        transform-origin:top;
        transition:transform 900ms ease-in-out;
        &.active
        {
            transform:translateY(0px);
        }
        input
        {
            border: none;
            background-color: transparent;
            outline: none;
            display: inline-block;
            color: $secondary-color-light;
            font-size: .8rem;
            font-weight: 600;
            letter-spacing: .03rem;
            width: 70%;
        }
        button
        {
            border: none;
            background-color: transparent;
            outline: none;
            color:$secondary-color-light;
            width: auto;
            font-size: 1.4rem;
            font-weight: 600;
        }
    }

}



::selection
{
    background-color: $primary-color;
    color: $secondary-color-light;
}
::placeholder 
{ /* Chrome, Firefox, Opera, Safari 10.1+ */
    color:$secondary-color-light;
    opacity: 1; /* Firefox */
}
  
:-ms-input-placeholder 
{ /* Internet Explorer 10-11 */
    color:$secondary-color-light;
}

::-ms-input-placeholder 
{ /* Microsoft Edge */
    color:$secondary-color-light;
}